* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
    padding: 20px;
    font-family: 'Karla', sans-serif;
}

main {
    background-color: #F5F5F5;
    max-height: 800px;
    max-width: 800px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.title {
    font-size: 40px;
    margin: 0;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 20;
    text-align: center;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.die {
  height: 50px;
  width: 50px;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.die-value {
    font-size: 2rem;
}

.face {
    display: grid;
    grid-template: repeat(3, 1fr) / repeat(3, 1fr);
}

.pip {
    background-color: black;
    border-radius: 50%;
}

.roll-button {
  width: 92.16px;
  height: 35.84px;
  margin-top: 25.6px;
  flex-shrink: 0;
  border: none;
  border-radius: 5px;
  background-color: #5035FF;
  color: #FFFFFF;
  cursor: pointer;
}
